;\n* {\n  box-sizing: border-box;\n}\na {\n  text-decoration: none;\n}\nhtml {\n  font-family: \"Helvetica Neue\", Helvetica, Arial, sans-serif;\n}\nbody,\ndiv,\ndl,\ndt,\ndd,\nul,\nol,\nli,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\npre,\ncode,\nform,\nfieldset,\nlegend,\ninput,\nbutton,\ntextarea,\np,\nblockquote,\nth,\ntd {\n  margin: 0;\n  padding: 0;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-weight: 500;\n  line-height: 1.426;\n}\narticle h1,\narticle h2,\narticle h3,\narticle h4,\narticle h5,\narticle h6 {\n  margin: 10px 0 15px 0;\n}\narticle h1, section h1 {\n  font-size: 2em;\n}\na,\na:hover,\na:active,\na:focus,\ninput,\ninput:hover,\ninput:active,\ninput:focus,\nselect,\nselect:hover,\nselect:active,\nselect:focus,\ntextarea,\ntextarea:hover,\ntextarea:active,\ntextarea:focus {\n  outline: none;\n}\nbutton,\nbutton:active,\nbutton:focus,\nbutton:hover {\n  outline: none;\n}\nul,\nol {\n  list-style: none;\n}\n\n/*\n * 回形针图标\n */\n/*\n * 观看图标\n */\n/*\n * 评论图标\n */\n/*\n * 喜欢图标\n */\n/*\n * 菜单图标\n */\n/*\n * 搜索图标\n */\n/*\n * 右箭头\n */\n/*\n * 下箭头\n */\n.tooltip {\n  position: relative;\n}\n.tooltip::after {\n    content: attr(original-title);\n    top: 0;\n    left: 50%;\n    -webkit-transform: translate(-50%, calc(-100% - 10px));\n            transform: translate(-50%, calc(-100% - 10px));\n    display: none;\n    font-size: 13px;\n    white-space: nowrap;\n    text-align: center;\n    position: absolute;\n    padding: 8px 10px;\n    color: #fff;\n    border-radius: 3px;\n    background-color: #444;\n    z-index: 9999;\n}\n.tooltip.rebound::after {\n    width: 200px;\n    white-space: normal;\n    line-height: 1.4;\n}\n.tooltip::before {\n    content: '';\n    display: none;\n    position: absolute;\n    top: -10px;\n    left: 50%;\n    width: 0;\n    height: 0;\n    border-width: 6px;\n    border-style: solid;\n    border-color: transparent;\n    border-top-color: #444;\n    -webkit-transform: translateX(-50%);\n            transform: translateX(-50%);\n}\n.tooltip:hover::after, .tooltip:hover::before {\n    display: block;\n}\n.container {\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -webkit-box-pack: center;\n      -ms-flex-pack: center;\n          justify-content: center;\n  -webkit-box-orient: vertical;\n  -webkit-box-direction: normal;\n      -ms-flex-direction: column;\n          flex-direction: column;\n  -ms-flex-wrap: wrap;\n      flex-wrap: wrap;\n  padding: 30px 30px 40px 30px;\n  background-color: #f4f4f4;\n}\n@media (max-width: 920px) {\n.container {\n      padding: 20px 30px;\n}\n}\n@media (max-width: 530px) {\n.container {\n      padding: 10px 0;\n}\n}\n.container .list {\n    display: -webkit-box;\n    display: -ms-flexbox;\n    display: flex;\n    -ms-flex-wrap: wrap;\n        flex-wrap: wrap;\n    margin: 0 auto;\n}\n.container .list > li {\n      margin-bottom: 15px;\n      width: 220px;\n      margin: 0 15px 15px 15px;\n}\n@media (max-width: 577px) {\n.container .list > li {\n          margin: 5px;\n          width: calc(50% - 10px);\n}\n.container .list > li .tools-group, .container .list > li .attribution {\n            display: none;\n}\n}\n.container .list > li:hover .badge {\n        opacity: 1;\n}\n@media (min-width: 2059px) {\n.container .list.small-mode {\n        width: auto;\n        max-width: auto;\n        display: -webkit-box;\n        display: -ms-flexbox;\n        display: flex;\n        -webkit-box-pack: center;\n            -ms-flex-pack: center;\n                justify-content: center;\n}\n}\n@media (max-width: 2059px) {\n.container .list.small-mode {\n        max-width: 1750px;\n}\n}\n@media (max-width: 1830px) {\n.container .list.small-mode {\n        max-width: 1500px;\n}\n}\n@media (max-width: 1580px) {\n.container .list.small-mode {\n        max-width: 1250px;\n}\n}\n@media (max-width: 1330px) {\n.container .list.small-mode {\n        max-width: 1000px;\n}\n}\n@media (max-width: 1080px) {\n.container .list.small-mode {\n        max-width: 750px;\n}\n}\n@med